<template>
  <div>
    <!-- 面包屑导航区 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片视图区 -->
    <el-card>
      <el-row :gutter="20">
        <!-- 搜索框 -->
        <el-col :span="8">
          <el-input
            @keyup.enter.native="getGoodsList"
            v-model="queryInfo.query"
            placeholder="请输入内容"
            clearable>
            <el-button slot="append" icon="el-icon-search" @click="getGoodsList"></el-button>
          </el-input>
        </el-col>
        <!-- 添加商品按钮 -->
        <el-col :span="4">
          <el-button type="primary" @click="goAddPage">添加商品</el-button>
        </el-col>
      </el-row>
      <!-- table表格区 -->
      <el-table
        :data="goodslist"
        stripe
        border
        style="width: 100%"
        >
        <!-- 索引列 -->
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column prop="goods_name" label="商品名称"></el-table-column>
        <el-table-column prop="goods_price" label="商品价格(元)" width="95"></el-table-column>
        <el-table-column prop="goods_weight" label="商品重量" width="70"></el-table-column>
        <el-table-column label="创建时间" width="140">
          <template slot-scope="scope">
            {{ scope.row.add_time | dateFormat }}
          </template>
        </el-table-column>
        <!-- 操作 -->
        <el-table-column label="操作" width="130">
          <template slot-scope="scope">
            <!-- TODO: 编辑 -->
            <el-button size="mini" type="primary" class="el-icon-edit"></el-button>
          <el-button size="mini" type="danger" class="el-icon-delete" @click="removeGoodsById(scope)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页区域 -->
      <el-pagination
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[10, 20, 35, 50]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'list',
  data() {
    return {
      // 商品查询的条件
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 10
      },
      // 商品列表
      goodslist: [],
      // 商品总数
      total: 0
    }
  },
  created() {
    this.getGoodsList()
  },
  methods: {
    // 获取商品列表
    async getGoodsList() {
      // 发送请求
      const { data: res } = await this.$http.get('/goods', { params: this.queryInfo })
      // 请求失败
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      // 请求成功
      this.$message.success(res.meta.msg)
      // 绑定数据
      this.goodslist = res.data.goods
      this.total = res.data.total
      // console.log(res.data)
    },
    // 监听分页size变化
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getGoodsList()
    },
    // 监听分页页码变化
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getGoodsList()
    },
    // 删除商品
    async removeGoodsById(scope) {
      // 弹出提示框
      const confirmResult = await this.$confirm('此操作将永久删除该商品, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err => err)
      // 取消删除
      if (confirmResult !== 'confirm') return this.$message.info('取消了删除操作！')
      // 确认删除，发送请求
      const { data: res } = await this.$http.delete('/goods/' + scope.row.goods_id)
      // 请求失败
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      // 请求成功
      this.$message.success(res.meta.msg)
      // 刷新数据
      this.goodslist.splice(scope.$index, 1)
    },
    // 跳转到添加商品页面
    goAddPage() {
      this.$router.push('/goods/add')
    }
  }
}
</script>

<style lang="less" scoped>

</style>
