<template>
  <div>
    <!-- 面包屑导航区 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>数据统计</el-breadcrumb-item>
      <el-breadcrumb-item>数据报表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片视图区 -->
    <el-card>
        <el-tabs>
          <el-tab-pane label="用户来源">
            <!-- 折线图 -->
            <div id="userSource" style="width:1200px;height:550px;"></div>
          </el-tab-pane>
          <el-tab-pane label="访问次数">
            <!-- 条形图 -->
            <div id="visits" style="width:1200px;height:550px;"></div>
          </el-tab-pane>
        </el-tabs>
    </el-card>
  </div>
</template>

<script>
import * as echarts from 'echarts'
import _ from 'lodash'
export default {
  name: 'report',
  data() {
    return {
      userSourceOptions: {
        title: {
          text: '用户来源'
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            label: {
              backgroundColor: '#E9EEF3'
            }
          }
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: [
          {
            boundaryGap: false
          }
        ],
        yAxis: [
          {
            type: 'value'
          }
        ]
      },
      visitsOptions: {
        title: {
          text: '访问次数'
        },
        legend: {},
        tooltip: {},
        dataset: {
          dimensions: ['product', '首页', '分类', '商品列表', '商品详情'],
          source: []
        },
        xAxis: { type: 'category' },
        yAxis: {},
        series: [
          { type: 'bar' },
          { type: 'bar' },
          { type: 'bar' },
          { type: 'bar' }
        ]
      }
    }
  },
  async mounted() {
    await this.getuserSourceOptions()
    const userSource = echarts.init(document.getElementById('userSource'))
    userSource.setOption(this.userSourceOptions)
    await this.getVisitsOptions()
    const visits = echarts.init(document.getElementById('visits'))
    visits.setOption(this.visitsOptions)
  },
  methods: {
    async getuserSourceOptions() {
      // 发送请求
      const { data: res } = await this.$http.get('/reports/type/1')
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.$message.success(res.meta.msg)
      // 合并数据
      this.userSourceOptions = _.merge(this.userSourceOptions, res.data)
    },
    async getVisitsOptions() {
      // 发送请求
      const { data: res } = await this.$http.get('/reports/type/2')
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.$message.success(res.meta.msg)
      // 处理数据
      const source = []
      const keys = Object.keys(res.data)
      keys.sort((a, b) => new Date(a) - new Date(b))
      for (const key of keys) {
        const obj = {
          product: key
        }
        res.data[key].forEach(item => {
          obj[item.rp2_page] = item.rp2_count
        })
        source.push(obj)
      }
      console.log(source)
      // 合并数据
      this.visitsOptions = _.merge(this.visitsOptions, { dataset: { source } })
      console.log(this.visitsOptions)
    }
  }
}
</script>

