<template>
  <div>
    <!-- 面包屑导航区 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>添加商品</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片视图区 -->
    <el-card>
      <!-- 提示区域 -->
      <el-alert :closable="false" title="添加商品信息" type="info" show-icon  center></el-alert>
      <!-- 步骤条区域 -->
      <el-steps :space="200" :active="activeIndex - 0" finish-status="success" align-center>
        <el-step title="基本信息"></el-step>
        <el-step title="商品参数"></el-step>
        <el-step title="商品属性"></el-step>
        <el-step title="商品图片"></el-step>
        <el-step title="商品内容"></el-step>
        <el-step title="完成"></el-step>
      </el-steps>
      <!-- 表单 -->
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="100px" class="demo-ruleForm" label-position="top">
        <!-- tab栏 -->
        <el-tabs
          @tab-click="tabClicked"
          :before-leave="beforeTabLeave"
          v-model="activeIndex"
          tab-position="left"
          style="height: 700px;">
          <!-- 基本信息 -->
          <el-tab-pane label="基本信息" name="0">
            <el-form-item label="商品名称" prop="goods_name">
              <el-input v-model="addForm.goods_name"></el-input>
            </el-form-item>
            <el-form-item label="商品价格" prop="goods_price">
              <el-input v-model="addForm.goods_price" type="number"></el-input>
            </el-form-item>
            <el-form-item label="商品重量" prop="goods_weight">
              <el-input v-model="addForm.goods_weight" type="number"></el-input>
            </el-form-item>
            <el-form-item label="商品数量" prop="goods_number">
              <el-input v-model="addForm.goods_number" type="number"></el-input>
            </el-form-item>
            <el-form-item label="商品分类" prop="goods_cat">
              <el-cascader
                v-model="addForm.goods_cat"
                :options="catelist"
                :props="cateProps"
                @change="handleChange">
              </el-cascader>
            </el-form-item>
          </el-tab-pane>
          <!-- 商品参数 -->
          <el-tab-pane label="商品参数" name="1">
            <el-form-item :label="item.attr_name" v-for="item in manyTableData" :key="item.attr_id">
              <el-checkbox-group v-model="item.attr_vals">
                <el-checkbox border :label="cb" v-for="(cb, i) in item.attr_values" :key="i"></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
          <!-- 商品属性 -->
          <el-tab-pane label="商品属性" name="2">
            <el-form-item :label="item.attr_name" v-for="item in onlyTableData" :key="item.attr_id">
              <el-input v-model="item.attr_vals"></el-input>
            </el-form-item>
          </el-tab-pane>
          <!-- 商品图片 -->
          <el-tab-pane label="商品图片" name="3">
            <el-upload
              multiple
              :on-success="handleSuccess"
              :headers="header"
              :action="uploadURL"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              list-type="picture">
              <el-button size="small" type="primary">点击上传</el-button>
            </el-upload>
          </el-tab-pane>
          <!-- 商品内容 -->
          <el-tab-pane label="商品内容" name="4">
            <!-- 富文本编辑器 -->
            <quill-editor
              v-model="addForm.goods_introduce"
            ></quill-editor>
            <!-- 添加商品按钮 -->
            <el-button type="primary" class="btnAdd" @click="add">添加商品</el-button>
          </el-tab-pane>
        </el-tabs>
      </el-form>
    </el-card>
    <!-- 图片预览 -->
    <el-dialog
      title="图片预览"
      :visible.sync="previewVisible"
      width="75%">
      <img class="previewImg" :src="previewPath" alt="">
    </el-dialog>
  </div>
</template>

<script>
import _ from 'lodash'
export default {
  name: '',
  data() {
    return {
      // 被激活的步骤下标
      activeIndex: '0',
      // 添加商品的表单
      addForm: {
        goods_name: '',
        goods_price: 0,
        goods_weight: 0,
        goods_number: 0,
        goods_cat: [],
        pics: [],
        goods_introduce: '',
        attrs: []
      },
      // 添加商品的表单绑定的规则
      addFormRules: {
        goods_name: [
          { required: true, message: '请输入商品名称', trigger: 'blur' }
        ],
        goods_price: [
          { required: true, message: '请输入商品价格', trigger: 'blur' }
        ],
        goods_weight: [
          { required: true, message: '请输入商品重量', trigger: 'blur' }
        ],
        goods_number: [
          { required: true, message: '请输入商品数量', trigger: 'blur' }
        ],
        goods_cat: [
          { required: true, message: '请选择商品分类', trigger: 'blur' }
        ]
      },
      // 分类数据
      catelist: [],
      // 级联选择器的绑定规则
      cateProps: {
        expandTrigger: 'hover',
        label: 'cat_name',
        value: 'cat_id'
      },
      // 参数列表
      manyTableData: [],
      // 属性列表
      onlyTableData: [],
      // 图片上传的请求头
      header: {
        Authorization: window.sessionStorage.getItem('token')
      },
      // 控制图片预览对话框的显示
      previewVisible: false,
      // 预览图片的路径
      previewPath: ''
    }
  },
  created() {
    this.getCatelist()
  },
  methods: {
    // 获取商品分类列表
    async getCatelist() {
      // 发送请求
      const { data: res } = await this.$http.get('/categories')
      // 请求失败
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      // 请求成功
      this.$message.success(res.meta.msg)
      // 绑定数据
      this.catelist = res.data
      // console.log(res.data)
    },
    // 级联选择器的触发事件
    handleChange() {
      if (this.addForm.goods_cat.length !== 3) {
        this.addForm.goods_cat = []
      }
    },
    // tab页切换的前置钩子
    beforeTabLeave(activeName, oldActiveName) {
      if (oldActiveName === '0') {
        if (this.addForm.goods_cat.length !== 3) {
          this.$message.error('请先选择商品分类')
          return false
        }
      }
    },
    // tab页签点击事件
    async tabClicked() {
      if (this.activeIndex === '1') {
        // 获取商品参数
        const { data: res } = await this.$http.get(`/categories/${this.cateId}/attributes`, { params: { sel: 'many' } })
        // 请求失败
        if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
        // 请求成功
        this.$message.success(res.meta.msg)
        // 处理参数值
        res.data.forEach(item => {
          item.attr_vals = item.attr_vals === ''
            ? [] : item.attr_vals.split(' ')
          item.attr_values = item.attr_vals
        })
        this.manyTableData = res.data
        // console.log(res.data)
      } else if (this.activeIndex === '2') {
        // 获取商品参数
        const { data: res } = await this.$http.get(`/categories/${this.cateId}/attributes`, { params: { sel: 'only' } })
        // 请求失败
        if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
        // 请求成功
        this.$message.success(res.meta.msg)
        this.onlyTableData = res.data
        // console.log(res.data)
      }
    },
    // 图片预览
    handlePreview(file) {
      this.previewPath = file.response.data.url
      this.previewVisible = true
    },
    // 图片移除
    handleRemove(file) {
      const filePath = file.response.data.tmp_path
      this.addForm.pics = this.addForm.pics.filter(item => item.pic !== filePath)
    },
    // 图片上传成功后的回调
    handleSuccess(res) {
      this.addForm.pics.push({ pic: res.data.tmp_path })
    },
    // 添加商品按钮触发事件
    add() {
      // 表单验证
      this.$refs.addFormRef.validate(async valid => {
        // 验证失败
        if (!valid) return this.$message.error('请填写表单的必要项')
        // 验证成功
        const form = _.cloneDeep(this.addForm)
        // 处理父级分类id
        form.goods_cat = form.goods_cat.join(',')
        // 处理参数
        this.manyTableData.forEach(item => form.attrs.push({
          attr_id: item.attr_id,
          attr_value: item.attr_vals.join(' ')
        }))
        // 处理属性
        this.onlyTableData.forEach(item => form.attrs.push({
          attr_id: item.attr_id,
          attr_value: item.attr_vals
        }))
        // console.log(form)
        // 发起请求
        const { data: res } = await this.$http.post('/goods', form)
        // 请求失败
        if (res.meta.status !== 201) return this.$message.error(res.meta.msg)
        // 请求成功
        this.$message.success(res.meta.msg)
        // 跳转页面
        this.$router.push('/goods')
      })
    }
  },
  computed: {
    cateId() {
      return this.addForm.goods_cat.length === 3
        ? this.addForm.goods_cat[2]
        : null
    },
    // 图片上传的地址
    uploadURL() {
      return this.$http.defaults.baseURL + 'upload'
    }
  }
}
</script>

<style lang="less" scoped>
.el-checkbox {
  margin: 0 10px 0 0 !important;
}
.previewImg {
  width: 100%;
}
.btnAdd {
  margin-top: 15px;
}
</style>
